    .mini-header{
        padding:0%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .UL-heading{
        color: #1F3453;
        text-align: center;
        font-size: 47.417px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        /* display: flex; */
    }

    .add-user-button {
        /* Other styles */
        padding: 13px 31px;
        flex-shrink: 0;
        font-size: 32.497px;
        border: none;
        color: white;
        border-radius: 11px;
        background: #F7921C;
        text-align: center;
        text-decoration: none;
        transition: background-color 0.3s ease; /* Smooth transition for background color */
    }
    
    .add-user-button:hover {
        background-color: #e6811b; /* Change background color on hover */
    }
    
    .users-grid{
        padding: 35px;
        padding-top: 10px;
    }

    .titles{
        padding-right: 40px;
        border-bottom: 2px solid #656464;
        display: flex;
        justify-content: space-around;
    }

    #Phone{
        justify-content: left;
    }

    .admin-table-headings{
        padding-inline: 1vw;
        justify-content: left;
        color: #656464;
        margin-block: 15px;
        font-size: 30px;
    }

    #smol.admin-table-headings{
        flex: 0.7;
    }
    #med.admin-table-headings{
        flex: 1;
    }
    #xs.admin-table-headings{
        flex: 0.6;
    }
    #actions.table-headings{
        justify-content: center;
    }
    #Phone{
        text-align: justify;
    }

    .user-data{
        padding-right: 20px;
        height: 68vh;
        overflow: hidden;
        overflow-y: scroll;
        margin-right: 10px;
    }

    ::-webkit-scrollbar{
        width: 15px;
    }
    ::-webkit-scrollbar-track{
        background-color: white;
        border-radius: 7.5px ;
        margin-block: 15px;
    }
    
    ::-webkit-scrollbar-thumb{
        border-radius: 7.5px;
        background: #B7B7B7;
    }